/* üåå Background and global typography */
body {
  min-height: 100vh;
  background: linear-gradient(135deg, #232526 0%, #414345 100%);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  margin: 0;
  font-family: 'Montserrat', 'Segoe UI', Arial, sans-serif;
}

/* üè† App container */
.app {
  background: rgba(40, 40, 50, 0.96);
  border-radius: 16px;
  box-shadow: 0 8px 32px rgba(0,0,0,0.28);
  padding: 32px 24px 24px 24px;
  max-width: 520px;
  width: 100%;
  position: relative;
  margin: 32px 0;
}

/* üß† Title */
h1 {
  font-family: 'Orbitron', 'Montserrat', Arial, sans-serif;
  font-size: 2.8rem;
  font-weight: 900;
  color: #ffd700;
  letter-spacing: 2px;
  text-shadow: 0 2px 12px #222, 0 0px 2px #fff;
  margin-bottom: 12px;
}

/* üßæ Header */
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 18px;
  font-size: 1.1rem;
  color: #fff;
}
.header label {
  font-weight: 600;
  margin-right: 6px;
}
.header select, .header button {
  font-family: inherit;
  font-size: 1rem;
  padding: 4px 10px;
  border-radius: 6px;
  border: none;
  margin-right: 6px;
  background: #232526;
  color: #ffd700;
  box-shadow: 0 2px 8px rgba(0,0,0,0.12);
  cursor: pointer;
  transition: background 0.18s;
}
.header button:hover, .header select:focus {
  background: #ffd700;
  color: #232526;
}

/* Undo button style */
.header .undo-btn {
  background: #ff4081;
  color: #fff;
  margin-left: 6px;
  font-weight: bold;
  box-shadow: 0 2px 8px rgba(255,64,129,0.18);
}
.header .undo-btn:hover {
  background: #fff;
  color: #ff4081;
}

/* üß© Board */
.board {
  display: grid;
  gap: 10px;
  background: #bbada0;
  padding: 16px;
  border-radius: 12px;
  box-shadow: 0 4px 24px rgba(0,0,0,0.18);
  margin: 0 auto 18px auto;
  transition: box-shadow 0.15s;
}
.board.size-3 { grid-template-columns: repeat(3, 1fr); }
.board.size-4 { grid-template-columns: repeat(4, 1fr); }
.board.size-5 { grid-template-columns: repeat(5, 1fr); }

/* üé® Tile */
.tile {
  aspect-ratio: 1 / 1;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 10px;
  box-shadow: 0 2px 12px rgba(0,0,0,0.18), inset 0 -3px rgba(0,0,0,0.22);
  font-size: clamp(20px, 5vw, 32px);
  font-family: 'Orbitron', 'Montserrat', Arial, sans-serif;
  font-weight: bold;
  letter-spacing: 1px;
  transition: transform 0.18s, background 0.18s, box-shadow 0.18s;
  background: rgba(238,228,218,0.35);
  color: #fff;
  position: relative;
  overflow: hidden;
}

/* üß± Tile colors */
.tile[data-value="2"]    { background: #eee4da; color: #776e65; }
.tile[data-value="4"]    { background: #ede0c8; color: #776e65; }
.tile[data-value="8"]    { background: #f2b179; color: #fff; }
.tile[data-value="16"]   { background: #f59563; color: #fff; }
.tile[data-value="32"]   { background: #f67c5f; color: #fff; }
.tile[data-value="64"]   { background: #f65e3b; color: #fff; }
.tile[data-value="128"]  { background: #edcf72; color: #fff; }
.tile[data-value="256"]  { background: #edcc61; color: #fff; }
.tile[data-value="512"]  { background: #edc850; color: #fff; }
.tile[data-value="1024"] { background: #edc53f; color: #fff; }
.tile[data-value="2048"] { background: #ffd700; color: #232526; }
.tile[data-value="4096"] { background: #3c3a32; color: #fff; }
.tile[data-value="8192"] { background: #1a1a1a; color: #fff; }

/* ‚ú® Glowing for high tiles */
.tile[data-value="128"],
.tile[data-value="256"],
.tile[data-value="512"],
.tile[data-value="1024"],
.tile[data-value="2048"],
.tile[data-value="4096"],
.tile[data-value="8192"] {
  box-shadow: 0 0 24px 6px rgba(255, 215, 0, 0.28);
  border: 2px solid #fffde7;
}

/* Merge grow effect */
.tile.merge {
  animation: merge-grow 0.38s cubic-bezier(.2,.8,.2,1);
  box-shadow: 0 0 32px 8px #ffd70088, 0 2px 12px rgba(0,0,0,0.18);
  z-index: 2;
}
@keyframes merge-grow {
  0% { transform: scale(1); }
  60% { transform: scale(1.18); }
  100% { transform: scale(1); }
}

/* New tile animation */
.new-tile {
  animation: pop 0.36s cubic-bezier(.2,.8,.2,1);
  background: linear-gradient(135deg,#fff59d,#ffd54f) !important;
  box-shadow: 0 6px 14px rgba(255,200,50,0.25);
}
@keyframes pop {
  0% { transform: scale(0.5); opacity: 0; }
  60% { transform: scale(1.08); opacity: 1; }
  100% { transform: scale(1); }
}

/* Direction indicator */
.board.move-up    { border: 3px dashed rgba(33,150,243,0.6); }
.board.move-down  { border: 3px dashed rgba(233,30,99,0.6); }
.board.move-left  { border: 3px dashed rgba(76,175,80,0.6); }
.board.move-right { border: 3px dashed rgba(255,152,0,0.6); }

/* Overlay message */
.overlay {
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-50%,-50%);
  background: #232526ee;
  color: #ffd700;
  font-size: 2rem;
  font-family: 'Orbitron', 'Montserrat', Arial, sans-serif;
  padding: 28px 36px;
  border-radius: 12px;
  box-shadow: 0 4px 24px rgba(0,0,0,0.22);
  text-shadow: 0 2px 8px #222;
  z-index: 10;
}

/* üéÆ Controls */
.controls {
  margin-top: 18px;
  display: flex;
  gap: 12px;
  justify-content: center;
}
.controls button {
  padding: 10px 18px;
  border-radius: 8px;
  border: none;
  cursor: pointer;
  font-size: 1.1rem;
  font-family: 'Orbitron', 'Montserrat', Arial, sans-serif;
  font-weight: 600;
  background: #ffd700;
  color: #232526;
  box-shadow: 0 2px 8px rgba(0,0,0,0.12);
  transition: background 0.18s, color 0.18s;
}
.controls button:hover {
  background: #ff4081;
  color: #fff;
}

/* Hide zero tiles */
.tile[data-value="0"] .tile-inner {
  opacity: 0;
}

/* üß© Instructions + Controls + Game Layout */
.app-row {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  gap: 24px;
  width: 100%;
  margin: 20px auto;
}

.instructions {
  background: rgba(40,40,50,0.85);
  color: #ffd700;
  border-radius: 12px;
  padding: 24px 18px;
  font-family: 'Montserrat', Arial, sans-serif;
  box-shadow: 0 2px 12px rgba(0,0,0,0.18);
  min-width: 180px;
  max-width: 320px;
  text-align: left;
}
.instructions h2 {
  font-size: 1.3rem;
  margin-bottom: 10px;
  color: #fff;
}
.instructions li {
  margin-bottom: 8px;
  font-size: 1rem;
}

.side-controls {
  display: flex;
  flex-direction: column;
  gap: 18px;
  align-items: center;
  margin-top: 20px;
}
.icon-btn {
  background: #232526;
  color: #ffd700;
  border: none;
  border-radius: 50%;
  width: 48px;
  height: 48px;
  font-size: 1.7rem;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 2px 8px rgba(0,0,0,0.18);
  cursor: pointer;
  transition: background 0.18s, color 0.18s, transform 0.18s;
}
.icon-btn:hover {
  background: #ffd700;
  color: #232526;
  transform: scale(1.12);
}

/* üì± Mobile layout */
@media (max-width: 900px) {
  .app-row {
    flex-direction: column;
    align-items: center;
    gap: 18px;
  }
  .instructions, .side-controls {
    margin-top: 0;
    width: 90vw;
    max-width: 100%;
  }
  .side-controls {
    flex-direction: row;
    justify-content: center;
    gap: 18px;
  }
}

/* üíª Desktop layout (‚â•900px): instructions | game | side-controls */
@media (min-width: 900px) {
  .app-row {
    flex-direction: row-reverse;
    align-items: flex-start;
    justify-content: center;
    gap: 32px;
    max-width: 1200px;
    margin: 0 auto;
  }

  .instructions {
    order: 0;
    flex: 0 0 320px;
    margin-top: 32px;
  }

  .app {
    order: 1;
    flex: 1 1 720px;
    max-width: 720px;
    margin: 0;
  }

  .side-controls {
    order: 2;
    flex: 0 0 88px;
    margin-top: 40px;
  }
}

/* Responsive app adjustments */
@media (max-width: 600px) {
  .app { padding: 18px 6px; max-width: 98vw; }
  h1 { font-size: 2rem; }
  .header { flex-direction: column; gap: 8px; font-size: 1rem; }
  .board { padding: 8px; gap: 6px; border-radius: 8px; }
  .tile { font-size: clamp(14px, 6vw, 22px); border-radius: 6px; }
  .instructions { padding: 12px 8px; font-size: 0.95rem; }
  .icon-btn { width: 38px; height: 38px; font-size: 1.2rem; }
  .overlay { font-size: 1.2rem; padding: 12px 18px; }
}
